import React, { Component } from 'react';
import '../assets/FormFour.scss';

const validate = values => {
    const errors = {}
    if (!values.nombre) {
        errors.nombre = 'Coloca tu nombre'
    }
    if (!values.email) {
        errors.email = 'Coloca tu email'
    }
    if (!values.texto) {
        errors.texto = 'Agrega un texto...'
    }
    return errors
}

class FormFour extends Component {

    state = {
        errors: {}
    }

    handleChange = ({ target }) => {
        const { name, value } = target
        this.setState({ [name]: value })
    }

    handleSubmit = e => {
        e.preventDefault()
        const { errors, ...sinErrors } = this.state
        const result = validate(sinErrors)

        // emailjs.sendForm('alejandromatiassch', 'template_turismo_ale', e.target, 'user_sFGeMInRraO3kCiz9ib04')
        // .then((result) => {
        //     console.log(result.text);
        // }, (error) => {
        //     console.log(error.text);
        // });
        // e.target.reset()

        this.setState({ errors: result })
        if (Object.keys(result).length === 0 ) {
            console.log('Formulario valido')
        }
        e.target.reset()
    }

    render() {
        const { errors } = this.state

        return(
            <div className="four_container">
                <form className='four_submit' onSubmit={this.handleSubmit} >
                    <input className='four_submit--name'
                            type="text" name="nombre" placeholder='Nombre' 
                            onChange={ this.handleChange } />
                    <input className='four_submit--surname'
                            type="text" name="email" placeholder='Email' 
                            onChange={ this.handleChange } />
                    <input className='four_submit--text'
                            type="text" name="texto" placeholder='Cuéntame algo...' 
                            onChange={ this.handleChange } />
                    <input className='four_submit--send'
                            type="submit" value="Enviar"/>                           
                    { errors.nombre && <p>{ errors.nombre }</p> }
                    { errors.email && <p>{ errors.email }</p> }
                    { errors.texto && <p>{ errors.texto }</p> }
                </form>
            </div>
        )
    }
}

export default FormFour;